.catalog {
    &Items {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 48px;
        row-gap: 80px;
        margin-top: 41px;
        margin-bottom: 61px;
        @media (max-width: 1080px) {
            grid-template-columns: 1fr;
        }
        @media (max-width: 810px) {
            row-gap: 40px;
        }
    }
}
